<template>
  <div class="itemBox">
    <p class="title">店铺公告</p>
    <el-form ref="form" :rules="rules" :model="itemData" size="mini">
      <el-form-item label="公告内容" prop="title">
        <el-input
          v-model="itemData.title"
          class="noticeInput"
          maxlength="200"
          placeholder="最多输入200个字"
          type="textarea"
        ></el-input>
      </el-form-item>

      <el-form-item label="文字颜色" class="showType bottomLine">
        <div class="sendType">
          <span class="showClass">推荐颜色</span>
          <div class="selctColor">
            <span
              class="bg1"
              @click="textColorChange(0)"
              :class="itemData.textColorSelect==0?'selsecColor':''"
            >
              <span></span>
            </span>
            <span
              class="bg2"
              @click="textColorChange(1)"
              :class="itemData.textColorSelect==1?'selsecColor':''"
            >
              <span></span>
            </span>
          </div>
        </div>
        <div class="sendType">
          <span class="showClass">自定义颜色</span>
          <div class="selctColor">
            <el-color-picker v-model="itemData.textColor" size="mini" @change="activeChange(1)"></el-color-picker>
            <el-button type="text" @click="resets(1)">重置</el-button>
          </div>
        </div>
      </el-form-item>
      <el-form-item label="背景颜色" class="showType">
        <div class="sendType">
          <span class="showClass">推荐颜色</span>
          <div class="selctColor">
            <span class="bg3" @click="bgChange(0)" :class="itemData.bgSelect==0?'selsecColor':''">
              <span></span>
            </span>
            <span class="bg4" @click="bgChange(1)" :class="itemData.bgSelect==1?'selsecColor':''">
              <span></span>
            </span>
          </div>
        </div>
        <div class="sendType">
          <span class="showClass">自定义颜色</span>
          <div class="selctColor">
            <el-color-picker
              v-model="itemData.backgroundColor"
              size="mini"
              @change="activeChange(2)"
            ></el-color-picker>
            <el-button type="text" @click="resets(2)">重置</el-button>
          </div>
        </div>
      </el-form-item>
    </el-form>

    <!-- {{itemData}} -->
  </div>
</template>

<script>
export default {
  props: {
    //父级传递的数据
    itemData: {
      type: Object
    }
  },
  data() {
    return {
      rules: {
        title: [{ required: true, message: "请输入公告内容", trigger: "blur" }]
      }
    };
  },
  methods: {
    //文字颜色变化
    textColorChange(e) {
      this.itemData.textColorSelect = e;
      switch (e) {
        case 0:
          this.itemData.textColor = "#fb3b06";
          break;
        case 1:
          this.itemData.textColor = "#666666";
          break;
      }
    },
    //背景颜色变化
    bgChange(e) {
      this.itemData.bgSelect = e;
      console.log(e);
      switch (e) {
        case 0:
          this.itemData.backgroundColor = "#fbf5dd";
          break;
        case 1:
          this.itemData.backgroundColor = "#ffffff";
          break;
      }
    },
    //颜色选择器
    activeChange(e) {
      if (e == 1) {
        if (this.itemData.textColor != "#fb3b06" || "#66666") {
          this.itemData.textColorSelect = 3;
        }
      } else {
        if (this.itemData.backgroundColor != "#fbf5dd" || "#ffffff") {
          this.itemData.bgSelect = 3;
        }
      }
    },

    //颜色重置
    resets(e) {
      if (e == 1) {
        this.itemData.textColor = "#fb3b06";
        this.itemData.textColorSelect = 0;
      } else {
        this.itemData.backgroundColor = "#fbf5dd";
        this.itemData.bgSelect = 0;
      }
    }
  }
};
</script>

<style scoped lang="less">
.active {
  border: 1px solid #000;
}
.selsecColor {
  border: 1px solid #007aff !important;
}
.title {
  border-bottom: 1px solid #eee;
  padding: 20px 18px 24px 18px;
  color: #333;
  font-size: 16px;
  font-weight: 700;
}
.itemBox /deep/ .el-form {
  margin: 18px 24px;
  .el-form-item__label {
    display: block;
    float: inherit;
    text-align: inherit;
    padding: 0;
    color: #222;
    font-weight: 100;
    margin-bottom: 12px;
  }
  .el-textarea__inner {
    height: 120px;
    border-radius: 2px;
    padding-left: 10px;
    padding-right: 10px;
  }
  .noticeInput {
    .el-input__inner {
      height: 32px;
      padding-left: 12px;
    }
  }
  .el-form-item__error {
    padding-top: 5px;
  }

  .custom {
    display: flex;
    align-items: center;
    .el-color-picker {
      margin-left: 16px;
    }
  }

  .showClass {
    width: 72px;
    height: 24px;
    color: #999999;
    font-size: 12px;
    text-align: left;
    margin-right: 24px;
    margin-left: 3px;
  }
  .sendType {
    display: flex;
    align-items: center;
    margin-top: 6px;
    .showClass {
      margin-right: 3px;
    }
    .selctColor {
      display: flex;
      align-items: center;
      .el-button {
        margin-left: 6px;
      }
    }
  }
}
.bottomLine {
  border-bottom: 1px solid #eee;
  padding-bottom: 24px;
}
.bg1,
.bg2,
.bg3,
.bg4 {
  display: inline-block;
  width: 22px;
  height: 22px;
  border: 1px solid #dddddd;
  background-color: #fff;
  text-align: center;
  line-height: 26px;
  margin-right: 12px;
  cursor: pointer;
  span {
    display: inline-block;
    width: 16px;
    height: 16px;
  }
}
.bg1 span {
  background-color: #fb3b06;
}
.bg2 span {
  background-color: #666;
}

.bg3 span {
  background-color: #fbf5dd;
}
.bg4 span {
  background-color: #fff;
}
</style>
